import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { environment } from 'src/environments/environment';
@Injectable({
  providedIn: 'root'
})
export class WeeklyGamesService {

  private baseUrl: string = environment.apiUrl;

  constructor(private http:HttpClient) { }

  getgameImages():Observable<any>{
    return this.http.get(this.baseUrl+'game/image-list').pipe(
      map(
        Response => {
          return Response;
        }
      )
    )

  }
  getCurrentWeekGame():Observable<any>{
    return this.http.get(this.baseUrl+'common/current-week-game').pipe(
      map(
        Response => {
          return Response;
        }
      )
    )

  }
  uploadgameImages(imageData):Observable<any>{
    return this.http.post(this.baseUrl+'game/add-edit',imageData).pipe(
      map(
        Response => {
          return Response;
        }
      )
    )

  }

  

  getParticipantsByGameId(gameId):Observable<any>{
    return this.http.get(this.baseUrl+'auth/participants-by-game/'+gameId).pipe(
      map(
        Response => {
          return Response;
        }
      )
    )

  }

  getCurrentGameNearestParticipants():Observable<any>{
    return this.http.get(this.baseUrl+'auth/current-game-nearest-participants').pipe(
      map(
        Response => {
          return Response;
        }
      )
    )

  }
}
