import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { map } from 'rxjs/internal/operators/map';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class JudgesService {
  private baseUrl: string = environment.apiUrl;

  constructor(private http:HttpClient) { }

  
  getJudgeCoords():Observable<any>{
    return this.http.get(this.baseUrl+'auth/current-game-judgements').pipe(
      map(
        Response => {
          return Response;
        }
      )
    )

  }
  getAllJudges():Observable<any>{
    return this.http.get(this.baseUrl+'judge/list').pipe(
      map(
        Response => {
          return Response;
        }
      )
    )

  }
  addJudge(judgeModel):Observable<any>{
    return this.http.post(this.baseUrl+'judge/add-edit',judgeModel).pipe(
      map(
        Response => {
          return Response;
        }
      )
    )

  }
  deleteJudge(judgeId):Observable<any>{
    return this.http.delete(this.baseUrl+'judge/delete/'+judgeId).pipe(
      map(
        Response => {
          return Response;
        }
      )
    )

  }

  
  getJudgeAveragePoint(gameId):Observable<any>{
    return this.http.get(this.baseUrl+'auth/set-average/'+gameId).pipe(
      map(
        Response => {
          return Response;
        }
      )
    )

  }
  
}
